.Board {
  height: 400px;
  background: #14bdac;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.Board__bottom {
  width: 100%;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.btn__reset {
  height: 30px;
  width: 65px;
  color: red;
  font-size: 24px;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

.btn__start {
  height: 30px;
  width: 65px;
  color: #14bdac;
  font-size: 24px;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

.square {
  width: 312px;
  height: 312px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.cell {
  width: 100px;
  height: 100px;
  border: 2px solid rgb(23,145,128);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 50px;
  cursor: pointer;
}

.wrapper {
  width: 100%;
  height: 462px;
  border: 1px solid lightgrey;
  border-radius: 10px;
}

.color__zero {
  color: white;
}

.title-result {
  position: absolute;
  top: 25%;
  font-size: 65px;
  color:rgb(255, 4, 4);
  -webkit-animation: dialogAnim 0.4s;
          animation: dialogAnim 0.4s;
}

@-webkit-keyframes dialogAnim {
  0% {
    top: -200px;
    opacity: 0;
  }
  100% {
    top: 150px;
    opacity: 1;
  }
}

@keyframes dialogAnim {
  0% {
    top: -200px;
    opacity: 0;
  }
  100% {
    top: 150px;
    opacity: 1;
  }
}
